"use client";

import { useRouter } from "next/navigation"; // ÂØºÂÖ• useRouter
import { useState } from 'react';
import { useAudioRecorder } from "./audio_recorder"; // ÂØºÂÖ•Ëá™ÂÆö‰πâÁöÑÈü≥È¢ëÂΩïÂà∂Âô®
import { setSpeechText } from '@/store/slices/speechSlice'
import { RootState } from '../store'; // ÂØºÂÖ• Redux store ÁöÑÁ±ªÂûã

import { useDispatch, useSelector } from 'react-redux'

const EnchanterSection = () => {
  const router = useRouter(); // ÂàùÂßãÂåñ useRouter
  const { isRecording, audioBlob, startRecording, stopRecording } = useAudioRecorder();
  const [isUploading, setIsUploading] = useState(false);

  const dispatch = useDispatch()
  const speechText = useSelector((state: RootState) => state.speech.speechText)

  const handleMouseDown = () => {
    startRecording();
  };

  const handleMouseUp = async () => {
    await stopRecording();
    // Á≠âÂæÖ audioBlob Êõ¥Êñ∞
    setTimeout(async () => {
      if (!audioBlob) return;
      await handleUpload();
    }, 100);
  };

  const handleUpload = async () => {
    if (!audioBlob) return;

    try {
      setIsUploading(true);
      const formData = new FormData();
      formData.append('audio', audioBlob, 'recording.webm');

      const response = await fetch('http://127.0.0.1:5000/transcribe', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error('Upload failed');
      }

      const data = await response.json();
      console.log('Upload successful:', data);
      
      dispatch(setSpeechText(data.text)); // Êõ¥Êñ∞ Redux store ‰∏≠ÁöÑÊñáÊú¨
      router.push('/enchanter'); // ‰∏ä‰º†ÊàêÂäüÂêéË∑≥ËΩ¨Âà∞ÁªìÊûúÈ°µÈù¢

    } catch (error) {
      console.error('Error uploading audio:', error);
    } finally {
      setIsUploading(false);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-6">
      <h1 className="text-4xl font-bold text-gray-800 mb-6">
      The English Enhancement Enchanter
      </h1>

      <p className="text-lg text-gray-600 mb-8 max-w-2xl text-center">
      Your words are precious gems that deserve to shine their brightest. Let us polish them to perfection, revealing their true native brilliance. Grammar, vocabulary, and structure blend together in harmony, as we craft expressions that flow as smoothly as a mountain stream.
      </p>

      <div className="flex flex-col items-center gap-4">
        <button
          onMouseDown={handleMouseDown}
          onMouseUp={handleMouseUp}
          onMouseLeave={handleMouseUp} 
          className={`px-6 py-3 rounded-lg font-semibold select-none ${
            isRecording 
              ? 'bg-red-500 hover:bg-red-600' 
              : 'bg-blue-500 hover:bg-blue-600'
          } text-white transition`}
        >
          {isRecording ? 'RECORDING...' : 'EXPRESS YOUR TRUE FEELINGS'}
        </button>

        {isUploading && (
          <div className="text-gray-600">
            LOADING...
          </div>
        )}
      </div>
    </div>
  );
};

export default EnchanterSection;